
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
name: 'userInfo',
components: {
Cell, Group, Flexbox, FlexboxItem, Actionsheet, PopupPicker
},
directives: {
TransferDom
},
data () {
return {
userName: '月光稀丶',
userImg: require('@/assets/images/mine-icon-bg.png'),
userRole: '0',
choseSex: '选择性别',
sexStatus: false,
sexList: {
male: '男',
female: '女'
},
province: [],
provinceList: [
{name: '北京市', value: 'bj', parent: 0},
{name: '上海市', value: 'sh', parent: 0},
{name: '上海市', value: 'sh1', parent: 0},
{name: '上海市', value: 'sh2', parent: 0},
{name: '上海市', value: 'sh3', parent: 0},
{name: '北京', value: 'bj01', parent: 'bj'},
{name: '上海', value: 'sh01', parent: 'sh'},
{name: '上海', value: 'sh01', parent: 'sh1'},
{name: '上海', value: 'sh01', parent: 'sh2'},
{name: '上海', value: 'sh01', parent: 'sh3'},
],
}
},
filters: {
roleFilter: (value) => {
switch (value) {
case '0':
return '主播'
case '1':
return '会员'
case '2':
return '讲师'
default:
return '普通用户'
}
}
},
methods: {
chooseSex () {
this.sexStatus = true
},
goUserName(){
this.$router.push({
name:'userName',
params:{
id:1
}
})
},
goTel(){
this.$router.push({
name:'bindTel',
params:{
type:"1"
}
})
},
click (key) {
switch (key) {
case 'male':
this.choseSex = '男'
break;
case 'female':
this.choseSex = '女'
break;
default:
}
},
change (value) {
console.log('pick选中至：', value)
}
}
}



